const BUY_TRANSACTION_TYPE = "buy";
const SELL_TRANSACTION_TYPE = "sell";
const PAID_TRANSACTION_TYPE = "income";
const BILLPAY_TRANSACTION_TYPE = "billpay";

class Transaction {
  constructor(
    date,
    type,
    amount_usd,
    amount_btc,
    price_usd,
    usd_balance,
    btc_balance,
    gain_loss
  ) {
    this.date = date;
    this.type = type;
    this.amount_usd = amount_usd;
    this.amount_btc = amount_btc;
    this.amount_btc_remaining = BUY_TRANSACTION_TYPE ? amount_btc : 0;
    this.price_usd = price_usd;
    this.usd_balance = usd_balance;
    this.btc_balance = btc_balance;
    this.gain_loss = type == SELL_TRANSACTION_TYPE ? gain_loss : 0;
  }
}

class YearlySummary {
  constructor(year) {
    this.year = year;
    this.bitcoin_accumulated = 0.0;
    this.bitcoin_sold = 0.0;
    this.realized_gain_loss = 0.0;
    this.bitcoin_retained = 0.0;
  }

  addAccumulated(amount_btc) {
    this.bitcoin_accumulated += amount_btc;
    this.bitcoin_retained += amount_btc;
  }

  addSold(amount_btc, gain_loss) {
    this.bitcoin_sold += amount_btc;
    this.realized_gain_loss += gain_loss;
    this.bitcoin_retained -= amount_btc;
  }
}

export class CheckingAccount {
  constructor(
    bitcoinPrices,
    initialBalanceUsd,
    biWeeklyIncomeUsd,
    spendingLimitUsd,
    billAmountUsd,
    costBasisMethod
  ) {
    this.bitcoinPrices = bitcoinPrices;
    this.usd_balance = initialBalanceUsd;
    this.bitcoin_balance = 0;
    this.bi_weekly_income_usd = biWeeklyIncomeUsd;
    this.spending_limit_usd = spendingLimitUsd + billAmountUsd;
    this.bill_amount_usd = billAmountUsd;
    this.cost_basis_method = costBasisMethod.toLowerCase();
    this.transactions = [];
    this.yearly_summaries = {};
  }

  addIncome(current_date) {
    this.usd_balance += this.bi_weekly_income_usd;
    this.transactions.push(
      new Transaction(
        this.getDateString(current_date),
        PAID_TRANSACTION_TYPE,
        this.bi_weekly_income_usd,
        0,
        0,
        this.usd_balance,
        this.bitcoin_balance,
        0
      )
    );
  }

  buyBitcoin(bitcoin_price, usd_amount, current_date) {
    if (this.usd_balance == 0)
      throw new Error("Oops, you ran out of fiat to buy Bitcoin!");

    if (this.usd_balance < usd_amount) usd_amount = this.usd_balance;

    let bitcoins_bought = usd_amount / bitcoin_price;
    this.bitcoin_balance += bitcoins_bought;

    if (this.bitcoin_balance > 21000000)
      throw new Error("Oops, you tried to buy more than 21M Bitcoin ;-)");

    this.usd_balance -= usd_amount;

    this.transactions.push(
      new Transaction(
        this.getDateString(current_date),
        BUY_TRANSACTION_TYPE,
        usd_amount,
        bitcoins_bought,
        bitcoin_price,
        this.usd_balance,
        this.bitcoin_balance,
        0
      )
    );

    const year = current_date.getFullYear();
    if (!this.yearly_summaries[year]) {
      this.yearly_summaries[year] = new YearlySummary(year);
    }
    this.yearly_summaries[year].addAccumulated(bitcoins_bought);
  }

  sellBitcoin(bitcoin_price, amount_usd, current_date) {
    const amount_btc = amount_usd / bitcoin_price;
    this.bitcoin_balance -= amount_btc;

    let realized_gain_loss = this.calculateGainLoss(
      amount_btc,
      bitcoin_price,
      this.getDateString(current_date)
    );

    this.usd_balance += amount_usd;

    this.transactions.push(
      new Transaction(
        this.getDateString(current_date),
        SELL_TRANSACTION_TYPE,
        amount_usd,
        amount_btc,
        bitcoin_price,
        this.usd_balance,
        this.bitcoin_balance,
        realized_gain_loss
      )
    );

    const year = current_date.getFullYear();
    if (!this.yearly_summaries[year]) {
      this.yearly_summaries[year] = new YearlySummary(year);
    }
    this.yearly_summaries[year].addSold(amount_btc, realized_gain_loss);
  }

  payBills(current_date) {
    if (this.usd_balance >= this.bill_amount_usd) {
      this.usd_balance -= this.bill_amount_usd;
      this.transactions.push(
        new Transaction(
          this.getDateString(current_date),
          BILLPAY_TRANSACTION_TYPE,
          this.bill_amount_usd,
          0,
          0,
          this.usd_balance,
          this.bitcoin_balance,
          0
        )
      );
    } else {
      throw new Error("Oops, you ran out of fiat to pay your bills!");
    }
  }

  calculateGainLoss(amount_btc, current_price, current_date) {
    let gain_loss = 0;
    let amount_to_sell = amount_btc;
    let eligible_buys = this.getEligibleBuys();

    while (amount_to_sell > 0 && eligible_buys.length > 0) {
      const transaction = eligible_buys.shift();
      if (transaction.amount_btc_remaining <= amount_to_sell) {
        gain_loss +=
          (current_price - transaction.price_usd) *
          transaction.amount_btc_remaining;
        amount_to_sell -= transaction.amount_btc_remaining;
        transaction.amount_btc_remaining = 0;
      } else {
        gain_loss += (current_price - transaction.price_usd) * amount_to_sell;
        transaction.amount_btc_remaining -= amount_to_sell;
        amount_to_sell = 0;
      }
    }

    return gain_loss;
  }

  getEligibleBuys() {
    let buys = this.transactions
      .slice()
      .filter(
        (t) => t.type == BUY_TRANSACTION_TYPE && t.amount_btc_remaining > 0
      );

    if (this.cost_basis_method === "fifo") {
      return buys;
    } else if (this.cost_basis_method === "lifo") {
      return buys.reverse();
    } else if (this.cost_basis_method === "hifo") {
      return buys.sort((a, b) => b.price_usd - a.price_usd);
    } else {
      throw Error("Invalid cost basis method");
    }
  }

  getBitcoinPrice(currentDate) {
    let date = this.getDateString(currentDate);
    if (!this.bitcoinPrices.has(date))
      throw new Error("No price data for " + date);

    return this.bitcoinPrices.get(date);
  }

  runLivingOnBtcSimulation({ startDate, endDate }) {
    const self = this;
    return this.runSimulation({
      startDate: startDate,
      endDate: endDate,
      onPayDay: function (bitcoin_price, date) {
        self.addIncome(date);
        self.buyBitcoin(bitcoin_price, self.usd_balance, date);
      },
      onBillDay: function (bitcoin_price, bill_amount_usd, date) {
        self.sellBitcoin(bitcoin_price, bill_amount_usd, date);
        self.payBills(date);
      },
    });
  }

  runLivingOnFiatSimulation({ startDate, endDate }) {
    const self = this;
    return this.runSimulation({
      startDate: startDate,
      endDate: endDate,
      onPayDay: function (bitcoin_price, date) {
        self.addIncome(date);
        const amount_to_spend = self.usd_balance - self.spending_limit_usd;
        if (amount_to_spend > 0)
          self.buyBitcoin(bitcoin_price, amount_to_spend, date);
      },
      onBillDay: function (bitcoin_price, bill_amount_usd, date) {
        self.payBills(date);
      },
    });
  }

  runFiatDailyBuysSimulation({ startDate, endDate }) {
    const self = this;
    return this.runSimulation({
      startDate: startDate,
      endDate: endDate,
      onPayDay: function (bitcoin_price, date) {
        self.addIncome(date);
      },
      onBillDay: function (bitcoin_price, bill_amount_usd, date) {
        self.payBills(date);
      },
      onDaily: function (bitcoin_price, balanceWhenLastPaid, date) {
        const amount_to_spend = balanceWhenLastPaid - self.spending_limit_usd;
        if (amount_to_spend > 0) {
          const dailyBuyAmount = amount_to_spend / 14;
          self.buyBitcoin(bitcoin_price, dailyBuyAmount, date);
        }
      },
    });
  }

  runSimulation({ startDate, endDate, onPayDay, onBillDay, onDaily }) {
    var currentDate = this.getDate(startDate);
    let end = this.getDate(endDate);
    let balanceWhenLastPaid = this.initialBalanceUsd;
    let daysUntilNextPay = 0; // Paid on the start date
    let daysUntilNextBill = 7; // Bills due a week after getting paid

    while (currentDate <= end) {
      let isPayDay = daysUntilNextPay == 0;
      let isBillDay = daysUntilNextBill == 0;
      let bitcoin_price = this.getBitcoinPrice(currentDate);

      if (isPayDay) {
        onPayDay(bitcoin_price, currentDate);
        balanceWhenLastPaid = this.usd_balance;
        daysUntilNextPay = 14;
        daysUntilNextBill = 7;
      }

      if (isBillDay) {
        onBillDay(bitcoin_price, this.bill_amount_usd, currentDate);
        daysUntilNextBill = 14;
      }

      if (onDaily) {
        onDaily(bitcoin_price, balanceWhenLastPaid, currentDate);
      }

      daysUntilNextPay--;
      daysUntilNextBill--;
      currentDate.setDate(currentDate.getDate() + 1);
    }

    return {
      summary: this.yearly_summaries,
      transactions: this.transactions,
    };
  }

  getDate(value) {
    let parts = value.split("-");
    // Please pay attention to the month (parts[1]); JavaScript counts months from 0:
    // January - 0, February - 1, etc.
    return new Date(parts[0], parts[1] - 1, parts[2]);
  }

  getDateString(date) {
    let month = (date.getMonth() + 1).toString().padStart(2, "0");
    let day = (date.getDate() + 0).toString().padStart(2, "0");
    return `${date.getFullYear()}-${month}-${day}`;
  }
}
