const Summary = ({ simulation }) => {
  const summary = simulation.summary;
  let runningBalance = 0;

  return (
    <div>
      <div className="SummaryContainer">
        <div className="SummaryTable">
          <table id="summary">
            <thead>
              <tr>
                <th>Year</th>
                <th>Bitcoin Stacked</th>
                <th>Bitcoin Spent</th>
                <th>Bitcoin Balance</th>
                <th>Realized Gains/Losses</th>
              </tr>
            </thead>
            <tbody>
              {Object.keys(summary).map((year, index) => {
                const accumulatedBTC = summary[year].bitcoin_accumulated;
                const soldBTC = summary[year].bitcoin_sold;
                summary[year].balance = runningBalance += accumulatedBTC;
                runningBalance -= soldBTC;

                return (
                  <tr key={index}>
                    <td>{year}</td>
                    <td>{accumulatedBTC.toFixed(8)}</td>
                    <td>{soldBTC.toFixed(8)}</td>
                    <td>{runningBalance.toFixed(8)}</td>
                    <td>
                      {new Intl.NumberFormat("en-US", {
                        style: "currency",
                        currency: "USD",
                      }).format(summary[year].realized_gain_loss)}
                    </td>
                  </tr>
                );
              })}
            </tbody>
            <tfoot>
              <tr>
                <th>Total</th>
                <th>
                  {Object.keys(summary)
                    .map((year) => summary[year].bitcoin_accumulated)
                    .reduce((total, value) => total + value, 0)
                    .toFixed(8)}
                </th>
                <th>
                  {Object.keys(summary)
                    .map((year) => summary[year].bitcoin_sold)
                    .reduce((total, value) => total + value, 0)
                    .toFixed(8)}
                </th>
                <th>
                  {Object.keys(summary)
                    .map((year) => summary[year].bitcoin_retained)
                    .reduce((total, value) => total + value, 0)
                    .toFixed(8)}
                </th>
                <th>
                  {new Intl.NumberFormat("en-US", {
                    style: "currency",
                    currency: "USD",
                  }).format(
                    Object.keys(summary)
                      .map((year) => summary[year].realized_gain_loss)
                      .reduce((total, value) => total + value, 0)
                  )}
                </th>
              </tr>
            </tfoot>
          </table>
        </div>
      </div>
      <h2>Transactions</h2>
      <div className="SummaryContainer">
        <div className="TransactionsTable">
          <table id="transactions">
            <thead>
              <tr>
                <th>Date</th>
                <th>Type</th>
                <th>Amount ($)</th>
                <th>Amount (₿)</th>
                <th>BTC-USD</th>
                <th>Realized Gain/Loss</th>
                <th>Balance ($)</th>
                <th>Balance (₿)</th>
              </tr>
            </thead>
            <tbody>
              {simulation.transactions.map((t) => (
                <tr>
                  <td>{t.date}</td>
                  <td>{t.type}</td>
                  <td>
                    {new Intl.NumberFormat("en-US", {
                      style: "currency",
                      currency: "USD",
                    }).format(t.amount_usd)}
                  </td>
                  <td>
                    {t.type == "buy" || t.type == "sell"
                      ? t.amount_btc.toFixed(8)
                      : ""}
                  </td>
                  <td>
                    {t.type == "buy" || t.type == "sell"
                      ? new Intl.NumberFormat("en-US", {
                          style: "currency",
                          currency: "USD",
                        }).format(t.price_usd)
                      : ""}
                  </td>
                  <td>
                    {t.type == "sell"
                      ? new Intl.NumberFormat("en-US", {
                          style: "currency",
                          currency: "USD",
                        }).format(t.gain_loss)
                      : ""}
                  </td>
                  <td>
                    {new Intl.NumberFormat("en-US", {
                      style: "currency",
                      currency: "USD",
                    }).format(t.usd_balance)}
                  </td>
                  <td>{t.btc_balance.toFixed(8)}</td>
                </tr>
              ))}
            </tbody>
          </table>
        </div>
      </div>
    </div>
  );
};

export default Summary;
