import React from "react";
import { Line } from "react-chartjs-2";
import {
  Chart as ChartJS,
  LineElement,
  CategoryScale,
  LinearScale,
  PointElement,
  Title,
  Tooltip,
  Legend,
} from "chart.js";

ChartJS.register(
  LineElement,
  CategoryScale,
  LinearScale,
  PointElement,
  Title,
  Tooltip,
  Legend
);

const BitcoinChart = ({ chartData }) => {
  const btcData = chartData.btcData.filter(
    (tx) => tx.type == "buy" || tx.type == "sell"
  );
  const fiatData = chartData.fiatData.filter(
    (tx) => tx.type == "buy" || tx.type == "sell"
  );
  const labels = btcData.map((entry) => entry.date);

  const data = {
    labels,
    datasets: [
      {
        label: "Living on Bitcoin",
        data: btcData,
        borderColor: "rgba(255, 165, 0, 1)",
        backgroundColor: "rgba(75, 192, 192, 0.2)",
        fill: true,
        borderWidth: 2,
      },
      {
        label: "Living on Fiat",
        data: mapToWeeklyTransactions(fiatData),
        borderColor: "rgba(133, 187, 101, 1)",
        backgroundColor: "rgba(75, 192, 192, 0.2)",
        fill: true,
        borderWidth: 2,
      },
    ],
  };

  const options = {
    responsive: true,
    parsing: {
      xAxisKey: "date",
      yAxisKey: "btc_balance",
    },

    plugins: {
      legend: {
        position: "top",
      },
      title: {
        display: true,
        text: "BTC Stacked",
      },
      tooltip: {
        callbacks: {
          afterBody: function (t, d) {
            // const data = t[0].raw;
            // return `Net worth: ${new Intl.NumberFormat("en-US", {
            //   style: "currency",
            //   currency: "USD",
            // }).format(data.price_usd * data.btc_balance)}`; //return a string that you wish to append
          },
        },
      },
    },
  };

  return <Line className="Chart" data={data} options={options} />;
};

// Function to calculate the start of the week relative to the first date
function getWeekStartDate(date, firstDate) {
  const d = new Date(date);
  const start = new Date(firstDate);
  const diff = Math.floor((d - start) / (1000 * 60 * 60 * 24 * 7)); // Weeks difference
  return new Date(start.setDate(start.getDate() + diff * 7));
}

function mapToWeeklyTransactions(transactions) {
  if (transactions.length === 0) return [];

  // Determine the start date of the first transaction
  const firstDate = transactions[0].date;

  const weeklyTransactions = {};

  transactions.forEach((tx) => {
    const weekStart = getWeekStartDate(tx.date, firstDate)
      .toISOString()
      .split("T")[0];

    // If there's no transaction recorded for this week, record this transaction
    if (!weeklyTransactions[weekStart]) {
      weeklyTransactions[weekStart] = tx;
    }
  });

  // Convert weeklyTransactions object to array format
  return Object.values(weeklyTransactions);
}

export default BitcoinChart;
